  .town-check {
    display: none;
  }
  
  .town-check + label {
    position: relative;
    margin-left: auto;
    margin-top:10px;
    margin-right:75px;
    cursor: pointer;
  }
  
  .town-check + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid #000000;
    background-color: #fff;
    box-sizing: border-box;
  }
  

  .town-check:checked + label:before {
    background-color: #a2f1be;
  }
