/* 스크롤바 전체 너비 설정 */
::-webkit-scrollbar {
  width: 13px;
}

/* 파이어폭스 스크롤바 너비 설정 */
html {
  scrollbar-width: thin;
}

/* 스크롤바 트랙(바탕) 스타일 */
::-webkit-scrollbar-track {
  background-color: none;
}

/* 스크롤바 썸 스타일 (움직이는 부분) */
::-webkit-scrollbar-thumb {
  background-color: #000000;
  border-radius: 5px;
}

/* 스크롤바 썸 호버 스타일 (마우스 오버 시) */
::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}
